.panel {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	align-items: center;
	height: 50px;
	width: 100%;
	background-color: #8e24aa;
}
.profileInformation {
	overflow: hidden;
	margin-right: auto;
	align-items: flex-start;
	margin-left: 10px;
	width: 50%;
}
.profileName {
	width: 100%;
	overflow: hidden;
	flex-basis: content;
	color: #ffffff;
	white-space: nowrap;
	font-family: helvetica;
	text-align: left;
	font-size: 20px;
	height: 60%;
}
.profileActivity {
	overflow: hidden;
	white-space: nowrap;
	width: 100%;
	height: 40%;
	flex-basis: content;
	color: rgba(255, 255, 255, 0.5);
	font-family: helvetica;
	text-align: left;
	font-size: 12px;
	min-height: 15px;
}
.profileAvatar {
	background-color: white;
	background-repeat: no-repeat;
	margin-left: 20px;
	height: 46px;
	width: 46px;
	border-radius: 23px;
	background-size: cover;
}
.profileDefault {
	width: 46px;
	height: 46px;
	fill: #8e24aa;
}
.contactsButton {
	height: 50px;
	width: 60px;
	padding: 5px;
	background-color: inherit;
	border: none;
	outline: none;
}
.contactsButton:hover {
	background-color: rgba(0, 0, 0, 0.08);
}
.contactsButton:active {
	background-color: #990099;
}
.menuButton {
	margin-right: 10px;
	height: 40px;
	width: 40px;
}
.menuGear {
	height: 40px;
	width: 40px;
	fill: #fff;
}
.contacts {
	display: flex;
	overflow: hidden;
	flex-flow: column nowrap;
	min-height: 100%;
	width: 100%;
	position: relative;
	min-width: 250px;
	justify-content: flex-start;
	align-items: center;
	animation: showC 0.5s ease-in;
}
.chatlist {
	height: 50px;
	width: 50px;
	fill: #fff;
}
@keyframes pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(255, 153, 255, 0.5);
	}
	70% {
		box-shadow: 0 0 0 10px rgba(255, 153, 255, 0.5);
	}
	100% {
		box-shadow: 0 0 0 10px rgba(255, 151, 255, 0);
	}
}
.contactsHead {
	display: flex;
	background-color: #8e24aa;
	width: 100%;
	min-width: 250px;
	height: 50px;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: center;
}
@keyframes headSlide {
	from {
		margin-bottom: 100%;
	}
	to {
		margin-bottom: 0px;
	}
}
.logo {
	min-width: 130px;
	font-weight: 700;
	height: 30px;
	margin-left: 80px;
	margin-right: auto;
	flex-basis: content;
	overflow: hidden;
	color: #ffffff;
	white-space: nowrap;
	font-family: helvetica;
	text-align: center;
	font-size: 25px;
	animation: headSlide 0.5s ease-out;
}
.searchButton {
	height: 30px;
	width: 30px;
	padding: 10px;
	min-width: 30px;
	animation: headSlide 0.5s ease-out;
}
.searchButton:hover {
	background-color: rgba(0, 0, 0, 0.08);
}
.searchButton:active {
	background-color: #990099;
}
.search {
	height: 30px;
	width: 30px;
	fill: #fff;
}
chat-bubble {
	width: 100%;
	border-bottom: 1px rgba(0, 0, 0, 0.5) solid;
}
.pulse {
	animation: pulse 1s linear;
}

.chatHead {
	display: flex;
	background-color: #8e24aa;
	width: 100%;
	min-width: 250px;
	height: 50px;
	flex-flow: row nowrap;
	justify-content: flex-start;
	align-items: center;
}
